"use client";
import { useState } from "react";
import { useGetGalleryQuery } from "@/redux/api/gallery";
import scss from "./GalleryMainContent.module.scss";
import Image from "next/image";

interface ZoomedImageProps {
  src: string;
  alt: string;
  onClose: () => void;
}

const ZoomedImage: React.FC<ZoomedImageProps> = ({ src, alt, onClose }) => {
  return (
    <div className={scss.zoomedImageOverlay} onClick={onClose}>
      <div className={scss.zoomedImageWrapper}>
        <Image
          src={src}
          alt={alt}
          layout="fill"
          objectFit="contain"
          quality={100}
        />
      </div>
    </div>
  );
};

const GalleryMainContent: React.FC = () => {
  const { data } = useGetGalleryQuery();
  const [zoomedImage, setZoomedImage] = useState<string | null>(null);

  const handleImageClick = (image: string) => {
    setZoomedImage(image);
  };

  const handleCloseZoom = () => {
    setZoomedImage(null);
  };

  return (
    <section className={scss.GalleryMainContent}>
      <div className="container">
        <div className={scss.content}>
          <div className={scss.galleryHead}>
            <h1>Фотогалерея</h1>
            <hr />
          </div>
          <div className={scss.gallery_card}>
            {data?.map((item: GALLERY.IGallery, index: number) => (
              <div key={index} className={scss.galleryItem}>
                <span onClick={() => handleImageClick(item.image)}>
                  <Image
                    src={item.image}
                    alt="img"
                    width={700}
                    height={500}
                    priority
                    quality={70}
                  />
                </span>
              </div>
            ))}
          </div>
        </div>
      </div>
      {zoomedImage && (
        <ZoomedImage
          src={zoomedImage}
          alt="Zoomed image"
          onClose={handleCloseZoom}
        />
      )}
    </section>
  );
};

export default GalleryMainContent;
